.card-name__container {
    height: 32px;
}

.card__list {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;

    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}

.card__item {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}

.card__image {
    border-radius: 10px;
}

.card-img-container {
    border-radius: 25px;
}

.card__description {
    box-sizing: border-box;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;

    border: 1px solid #eeeeee;

    display: inline-block;
    vertical-align: middle;
}

.card__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.2;
    text-transform: uppercase;
}

.card__info {
    color: $auxiliary-text-color;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.2;
}

.card__link {
    display: flex;
    flex-direction: column;

    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
    }
}

@media screen and (max-width: 767px) {
    .card__list {
        display: block;
    }

    .card__item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
    .card__list {
        display: flex;
        flex-wrap: wrap;

        margin-top: calc(-1 * 30px);
        margin-left: calc(-1 * 30px);
    }

    .card__item {
        display: flex;
        box-sizing: border-box;

        flex-basis: calc(100% / 2 - 30px);

        margin-left: 30px;
        margin-top: 30px;

        &:nth-child(2) {
            .card__description {
                padding-right: 10px;
            }
        }
    }
}

@media screen and (min-width: 1280px) {
    .card__list {
        display: flex;
        flex-wrap: wrap;

        margin-top: calc(-1 * 30px);
        margin-left: calc(-1 * 30px);
    }

    .card__item {
        box-sizing: border-box;

        flex-basis: calc(100% / 3 - 30px);

        margin-left: 30px;
        margin-top: 30px;
    }
}
