.modal {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: auto;
}

.modal_content {
  position: relative;
  width: 280px;
  height: 100vh;
  padding: 48px 20px 41px 20px;
  z-index: 999;
  background-color: #fff;
  overflow: auto;
  border-radius: 5px;
  color: #010101;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  @media screen and (min-width: 768px) {
    padding: 40px 30px;
    display: flex;
    width: 618px;
    height: 562px;
    margin: 0 auto;
  }
  @media screen and (min-width: 1024px) {
    padding: 50px 30px;
    width: 882px;
    height: 568px;
    margin: 0 auto;
  }
}

.film-info {
  display: inline-block;
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
  }
}

.modal-card-poster {
  display: block;
  border-radius: 5px;
  width: 240px;
  @media screen and (min-width: 768px) {
    width: 264px;
    margin-right: 30px;
  }
  @media screen and (min-width: 1024px) {
    width: 396px;
  }
}

.modal-card {
  display: inline-block;
  width: 240px;
  @media screen and (min-width: 768px) {
    width: 264px;
  }
  @media screen and (min-width: 1024px) {
    width: 396px;
  }
}

.modal-card-title {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
  text-transform: uppercase;
  margin-bottom: 20px;
  margin-top: 30px;
  @media screen and (min-width: 768px) {
    margin-top: 0;
  }
  @media screen and (min-width: 1024px) {
    font-size: 30px;
    line-height: 1.666;
  }
}

.modal-card-information {
  display: flex;
  margin-bottom: 20px;
}

.left-part,
.right-part {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.333;
}

.left-part {
  color: #8c8c8c;
  white-space: nowrap;
}

.right-part {
  padding-left: 40px;
}

.close_modal_window {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: background-color var(--animation-duration) var(--timing-function);
  background-image: url('../images/icon-close.svg');
  background-position: center;
  background-size: 60%;
  background-repeat: no-repeat;
}

.modal-open {
  height: 100vh;
  overflow-y: hidden;
}
